body{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: rgb(172, 239, 209);
}
button{
  color: rgb(0, 215, 143);
  font-weight: bold;
  font-size: 25px;
  border: 2px solid rgb(0, 167, 86);
}
.row{
  margin: 0px 0px;
  padding: 0px 0px;
  width: 50vw;
  height: 50vh;
}
#drum-machine{
  width: 50vw;
  height: 50vh;
  display: flex;
  align-items: center;
  flex-direction: column; 
}
#display{
  font-size: 30px;
  width: 40px;
  background-color: rgb(0, 128, 100);
  color: rgb(172, 239, 211);
  margin-bottom: 20px;
}